/* container */
/* global */
/* FONTS */
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  src: url("../../fonts/Montserrat-Regular.woff2") format("woff2"), url("../../fonts/Montserrat-Regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 500;
  src: url("../../fonts/Montserrat-SemiBold.woff2") format("woff2"), url("../../fonts/Montserrat-SemiBold.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 600;
  src: url("../../fonts/Montserrat-Medium.woff2") format("woff2"), url("../../fonts/Montserrat-Medium.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 700;
  src: url("../../fonts/Montserrat-Bold.woff2") format("woff2"), url("../../fonts/Montserrat-Bold.woff") format("woff");
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  height: 100%;
}

body {
  display: flex;
  margin: 0;
  flex-direction: column;
  min-width: 320px;
  min-height: 100%;
  font-family: "Montserrat", "Arial", sans-serif;
  font-style: normal;
}

ol,
ul,
dl {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding: 0 22px;
}
@media (min-width: 768px) {
  .container {
    max-width: 730px;
    padding: 0;
  }
}
@media (min-width: 1440px) {
  .container {
    max-width: 1360px;
    padding: 0;
  }
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.tag-line {
  margin: 0;
  padding: 0;
  font-weight: 700;
  font-size: 12px;
  line-height: 16px;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: #7859cf;
}

/* components */
/* HEADER */
.header {
  background-color: #ffffff;
}

.navigation {
  position: relative;
  display: grid;
  grid-template-columns: 2fr 3fr 1fr;
}

.navigation__logo {
  display: flex;
  width: 60px;
  height: 60px;
  align-items: center;
}

.navigation__logo-image {
  padding-top: 7px;
  transition: all 0.5s ease-in-out;
}

.navigation__logo-image:hover,
.navigation__logo-image:focus,
.navigation__logo-image:active {
  opacity: 0.85;
}

.navigation__logo-text {
  display: none;
}

.navigation__link {
  display: flex;
  width: 47px;
  height: 60px;
  justify-content: center;
  align-items: center;
  font-family: inherit;
  font-weight: 500;
  font-size: 12px;
  line-height: 16px;
  text-align: center;
  letter-spacing: 0.02em;
  text-transform: uppercase;
  color: #333333;
}

.navigation__item:hover .navigation__link-icon,
.navigation__item:hover .navigation__link,
.navigation__item:focus .navigation__link {
  color: #7859cf;
  transition: all 0.5s ease-in-out;
}

.navigation__item:active,
.navigation__button:active {
  background-color: #f0ebff;
  transition: all 0.5s ease-in-out;
}

.navigation__link-icon {
  width: 16px;
  height: auto;
  padding-top: 1px;
}

.navigation-users {
  display: flex;
  gap: 12px;
  flex-wrap: wrap;
  justify-content: flex-end;
}

.navigation__search-text,
.navigation__cart-text {
  display: none;
}

.navigation__button {
  position: relative;
  display: flex;
  width: 46px;
  height: auto;
  padding: 0;
  background-color: transparent;
  border: none;
  transition: 0.5s;
  transform: translate(13px, 0);
  animation: navClose 0.5s ease-in-out;
  cursor: pointer;
}

.navigation__button::before {
  content: "";
  position: absolute;
  top: 24px;
  right: 13px;
  width: 16px;
  height: 2px;
  background-color: #333333;
  box-shadow: 0 5px 0 0 #333333, 0 10px 0 #333333;
}

.header__opened .navigation__button::before,
.header__opened .navigation__button::after {
  content: "";
  position: absolute;
  top: 29px;
  right: 13px;
  width: 17px;
  height: 2px;
  background-color: #333333;
  box-shadow: none;
}

.header__opened .navigation__button::before {
  transform: rotate(-45deg);
}

.header__opened .navigation__button::after {
  transform: rotate(45deg);
}

.navigation-site {
  position: absolute;
  display: grid;
  flex-wrap: wrap;
  top: 60px;
  width: 100%;
  z-index: 1;
  background-color: #ffffff;
  transition: 0.5s ease-in-out;
}
.navigation-site .navigation__item {
  display: flex;
  justify-content: center;
  border-top: 2px solid #f0ebff;
}
.navigation-site .navigation__item-last {
  border-bottom: 2px solid #f0ebff;
}
.navigation-site .navigation__link {
  height: 55px;
}

.header__closed .navigation-site {
  display: none;
}

@media (max-width: 767.98px) {
  .header__nojs {
    height: 286px;
    overflow: hidden;
  }
  .header__nojs .navigation-site {
    display: block;
    position: relative;
    width: 130vw;
    transform: translate(-40vw, 0);
    transition: opacity 0.3s ease-in-out;
  }
  .header__nojs .navigation-users {
    position: absolute;
    right: 6.3vw;
  }
  .header__nojs .navigation__button {
    display: none;
  }
}
@media (min-width: 768px) {
  .navigation {
    grid-template-columns: 1fr 3fr 1fr;
  }
  .navigation__logo {
    width: 82px;
    height: 78px;
    border-bottom: 2px solid transparent;
  }
  .navigation__logo-image {
    width: auto;
    padding-top: 10px;
  }
  .header__closed .navigation-site {
    display: flex;
  }
  .navigation-site {
    position: relative;
    top: 0;
    display: flex;
    margin-left: 3px;
    gap: 0;
  }
  .navigation-site .navigation__item {
    border: none;
  }
  .navigation-site .current-link {
    color: #7859cf;
    border-bottom: 2px solid #7859cf;
  }
  .navigation-site .navigation-users {
    margin-right: 2px;
    justify-content: flex-end;
    gap: 0;
  }
  .navigation-site .navigation__link {
    margin: auto;
    width: 103px;
    height: 78px;
    border-bottom: 2px solid transparent;
  }
  .navigation-users {
    gap: 1px;
    justify-content: flex-end;
  }
  .navigation__user,
  .navigation__bags {
    width: 60px;
    height: 80px;
  }
  .navigation__link-icon {
    height: auto;
  }
  .navigation__button {
    display: none;
  }
}
@media (min-width: 1440px) {
  .navigation {
    grid-template-columns: 1fr 3fr 2fr;
  }
  .navigation__logo {
    width: 325px;
    gap: 18px;
  }
  .navigation__logo-image {
    width: 310px;
  }
  .navigation__logo-text {
    display: flex;
    font-weight: 400;
    font-size: 12px;
    line-height: 16px;
    color: #333333;
  }
  .navigation-site {
    width: auto;
    margin: auto;
    gap: 19px;
    transform: translate(77px, 0);
  }
  .navigation-users {
    gap: 18px;
  }
  .navigation__bags {
    gap: 8px;
    width: 123px;
  }
  .navigation__user {
    gap: 8px;
    width: 123px;
  }
  .navigation__cart-text {
    display: flex;
  }
  .navigation__search-text {
    display: flex;
  }
}
.slider {
  z-index: 0;
  overflow: hidden;
}

.swiper {
  width: auto;
  height: auto;
}

.swiper-slide {
  padding-top: 20px;
  padding-bottom: 44px;
  min-height: 656px;
}

.slide-white {
  background-color: #f3ebe1;
}

.slide-purple {
  background-color: #eae6fc;
}

.slide-black {
  background-color: #e5e6e8;
}

.slide__picture {
  display: flex;
  justify-content: center;
}

.slide__picture-image {
  margin-bottom: 12px;
}

.slider__scroll-prev,
.slider__scroll-next {
  top: 3.5%;
  height: 100%;
  color: #7859cf;
}

.slider__scroll-prev:hover,
.slider__scroll-prev:focus {
  background: linear-gradient(90deg, #ffffff 0%, rgba(255, 255, 255, 0) 100%);
}

.slider__scroll-next:hover,
.slider__scroll-next:focus {
  background: linear-gradient(90deg, rgba(255, 255, 255, 0) 0%, #ffffff 100%);
}

.slider__scroll-prev:active {
  background: linear-gradient(90deg, rgba(120, 89, 207, 0.25) 0%, rgba(120, 89, 207, 0) 100%);
}

.slider__scroll-next:active {
  background: linear-gradient(90deg, rgba(120, 89, 207, 0) 0%, rgba(120, 89, 207, 0.25) 100%);
}

.slider__scroll-prev {
  left: -2.5px;
}

.slider__scroll-next {
  right: -2.5px;
}

.slider__scroll-disabled {
  color: #bdbdbd;
}

.slider__scroll-prev::after,
.slider__scroll-next::after {
  font-size: 17px;
}

.slide__title {
  margin-top: 5px;
  margin-bottom: 10px;
  font-family: inherit;
  font-weight: 700;
  font-size: 36px;
  line-height: 48px;
  text-align: center;
  letter-spacing: 0.02em;
  color: #333333;
}

.slide__text {
  font-family: inherit;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  text-align: center;
  color: #333333;
  margin-top: 2px;
  margin-bottom: 4px;
}

.slide__order {
  display: flex;
  margin-left: 25px;
  gap: 7px;
  align-items: center;
  flex-wrap: wrap;
}

.order__old-price {
  font-family: inherit;
  font-weight: 400;
  font-size: 16px;
  line-height: 32px;
  text-decoration-line: line-through;
  color: #7859cf;
}

.order__new-price {
  font-family: inherit;
  font-weight: 600;
  font-size: 20px;
  line-height: 32px;
  color: #333333;
}

.order__button {
  margin-left: 14px;
}

.slider__pagination {
  display: none;
}

@media (min-width: 768px) {
  .swiper {
    padding-bottom: 198px;
  }
  .swiper-slide {
    min-height: 640px;
    height: 640px;
  }
  .slide-container {
    display: flex;
    flex-direction: column;
  }
  .slide__info {
    order: 1;
    padding-top: 40px;
    padding-bottom: 5px;
  }
  .slide__picture {
    order: 2;
    margin: auto;
    width: 62vw;
    height: auto;
  }
  .slide__picture-image {
    width: 475px;
    height: 474px;
  }
  .slide__title {
    margin-top: 4px;
    margin-bottom: 14px;
    font-size: 56px;
    line-height: 80px;
  }
  .slide__text {
    width: 80%;
    margin: auto;
    margin-bottom: 6px;
    font-size: 20px;
    line-height: 32px;
  }
  .slide__order {
    margin: auto;
    justify-content: center;
  }
  .order__old-price {
    font-size: 16px;
    line-height: 32px;
  }
  .order__new-price {
    font-size: 20px;
    line-height: 32px;
    margin-right: 15px;
  }
  .slider__scroll-next,
  .slider__scroll-prev {
    top: 2%;
    width: 11%;
    height: 77%;
  }
  .slider__scroll-prev {
    left: -2px;
  }
  .slider__scroll-next {
    right: 0;
  }
  .slider__scroll-next::after,
  .slider__scroll-prev::after {
    font-size: 20px;
  }
}
@media (min-width: 1440px) {
  .swiper {
    padding-bottom: 0;
  }
  .slide-container {
    flex-direction: row;
    padding: 10px 60px 28px 66px;
    gap: 30px;
  }
  .swiper-slide {
    height: auto;
  }
  .slide__picture {
    width: 1580px;
    order: 1;
  }
  .slide__picture-image {
    width: 610px;
    height: 600px;
  }
  .slide__info {
    order: 2;
    padding-top: 150px;
  }
  .slide__title,
  .slide__text {
    width: auto;
    text-align: start;
  }
  .slide__text {
    margin-bottom: 25px;
  }
  .slide__order {
    justify-content: flex-start;
  }
  .slider__scroll-next,
  .slider__scroll-prev {
    top: 3%;
    width: 6%;
    height: 100%;
  }
  .slider__pagination {
    display: flex;
    transform: translate(0, -62px);
    justify-content: center;
  }
  .slider__pagination .swiper-pagination-bullet.swiper-pagination-bullet-active {
    width: 62px;
    height: 4px;
    border-radius: 0;
    background-color: #7859cf;
  }
  .slider__pagination .swiper-pagination-bullet {
    width: 62px;
    height: 4px;
    opacity: 1;
    border-radius: 0;
    background-color: #ffffff;
  }
  .slider__pagination .swiper-pagination-bullet:hover,
  .slider__pagination .swiper-pagination-bullet:focus {
    background-color: rgba(120, 89, 207, 0.25);
  }
}
.tag-line {
  text-align: center;
}
@media (min-width: 1440px) {
  .tag-line {
    text-align: left;
  }
}

