/* FORM */
.form__fieldset {
  margin: 0;
  padding: 0;
  border: none;
}

.form__fieldset-checkbox {
  margin-bottom: 7px;
}

.form__fieldset-legend {
  margin-bottom: 21px;
  padding: 0;
  font-weight: 600;
  font-size: 16px;
  line-height: 24px;
  color: #333333;
}

.form__fieldset-legend-checkbox {
  margin-bottom: 20px;
}

.form__fieldset-list {
  display: flex;
  flex-direction: column;
  gap: 21px;
}

.range__wrapper {
  display: flex;
  align-items: center;
  min-height: 24px;
  margin-bottom: 26px;
}

.range__scale {
  position: relative;
  width: 100%;
  height: 4px;
  background-color: #e2e2e2;
  border: none;
  box-shadow: none;
}

.range__bar .range__toggle {
  display: none;
}

.range__scale .range__bar {
  display: block;
  position: absolute;
  width: 93%;
  height: 4px;
  background-color: #9070ec;
}

.noUi-connect {
  width: 100%;
}

.range__scale .range__toggle {
  display: block;
  position: absolute;
  top: -9px;
  width: 24px;
  height: 24px;
  background: #ffffff;
  border-radius: 50%;
  border: 2px solid #9070ec;
}

.range__toggle::after {
  content: "";
  position: absolute;
  top: -2px;
  left: -1px;
  width: 24px;
  height: 24px;
  border-radius: 50%;
  transform: scale(0);
  transition-duration: 0.4s;
  transition-property: transform;
}

.toggle-max::after {
  left: -2px;
}

.range__toggle:hover::after,
.range__toggle:focus::after {
  transform: scale(0.75) translate(0, 1px);
  background-color: rgba(120, 89, 207, 0.25);
}

.range__toggle:active::after {
  transform: scale(0.75) translate(0, 1px);
  background-color: #7859cf;
}

.toggle-max {
  right: 0;
}

.toggle-max:hover::after,
.toggle-max:focus::after,
.toggle-max:active::after {
