}

.radio__item {
  position: relative;
  margin-left: 33px;
}

.radio__label,
.checkbox__label {
  position: relative;
  display: block;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: #333333;
  cursor: pointer;
}

.radio__label::before,
.radio__label::after {
  content: "";
  position: absolute;
  top: 0.1vw;
  left: -34px;
  width: 24px;
  height: 24px;
  border-radius: 50%;
  border: 2px solid #bdbdbd;
  transition-duration: 0.4s;
  transition-property: transform;
}

.radio__label::after {
  background-color: #7859cf;
  border: none;
  transform: scale(0);
}

.radio__input:checked + .radio__label::before {
  border: 2px solid #7859cf;
}

.radio__input:checked + .radio__label::after {
  transform: scale(0.75);
}

.radio__label:hover::before,
.radio__label:focus::before {
  border: 2px solid #7859cf;
}

.radio__label:hover::after,
.radio__label:focus::after {
  background-color: rgba(120, 89, 207, 0.25);
  transform: scale(0.7);
}

.checkbox__item {
  margin-left: 53px;
}

.checkbox__span {
  position: relative;
}

.checkbox__span::before {
  content: "";
  position: absolute;
  left: -53px;
  width: 42px;
  height: 24px;
  background-color: #bdbdbd;
  border-radius: 12px;
  transform: translate(0, 1px);
  transition: 0.4s ease-in-out;
}

.checkbox__span::after {
  content: "";
  position: absolute;
  left: -51px;
  border: none;
  width: 20px;
  height: 20px;
  background-color: #ffffff;
  border-radius: 50%;
  transform: translate(0, 3px);
  transition: 0.4s ease-in-out;
}

.checkbox__span:hover::before,
.checkbox__span:focus::before {
  background-color: rgba(120, 89, 207, 0.25);
  border: 2px solid #9070ec;
}

.checkbox__input:checked + .checkbox__span::before {
  background-color: #7859cf;
}

.checkbox__input:checked + .checkbox__span::after {
  transform: translate(18px, 3px);
}

