  background-position: right 10px center;
  cursor: pointer;
}

.select__control-title {
  position: relative;
  margin: 0;
  padding: 3px 30px 3px 10px;
  font-family: inherit;
  font-weight: 600;
  font-size: 16px;
  line-height: 24px;
