.select__control-title::after {
  content: "";
  position: absolute;
  width: 20px;
  height: 25px;
  background-image: url("../../img/arrow.svg");
  background-repeat: no-repeat;
  background-position: center;
}

.select__control:hover,
.select__control:focus,
.select__control-title:hover,
.select__control-title:focus {
  background-color: #f0ebff;
  border-radius: 4px;
}

.select__control-div {
  display: flex;
  position: relative;
  margin: 0;
  padding: 0;
}

.select__control-div[data-state=active] .select__control-content {
  display: flex;
}
.select__control-div[data-state=active] .select__control-title:active::after {
  transform: rotate(180deg);
}
.select__control-div[data-state=active] .select__control-input + .select__control-label {
  min-height: 40px;
}

.select__control-content {
  position: absolute;
  top: 32px;
  right: 0;
  display: none;
  flex-direction: column;
  width: 120%;
  height: auto;
  transition: all 0.3s ease-out;
